.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ui.header>img.icon-italy {
  display: block;
  padding: 5px;
  width: 5em;
  margin-left: 2.5em;
}

@media only screen and (max-width: 500px){

  html{
    resize: both;
    overflow: auto;
  }

  .ui.segment.all-country{
    padding-top: '3.2em';
  }

  .ui.small.three.statistics.all-country{
    display: grid;
  }

  .ui.segment.italy-data{
    padding-top: '3.2em';
  }

  .ui.small.three.statistics.italy-data{
    display: grid;
  }
}